.animated-button {
  background-color: inherit;
  color: #585858;
  cursor: pointer;
  border: 1px solid #0000;
  border-radius: 100px;
  align-items: center;
  gap: 4px;
  padding: 10px 46px;
  font-family: Merriweather Sans, sans-serif;
  font-size: 14px;
  font-weight: 500;
  transition: all .6s cubic-bezier(.23, 1, .32, 1);
  display: flex;
  position: relative;
  overflow: hidden;
  box-shadow: 0 0 0 2px #585858;
}

.animated-button svg {
  fill: #f39a74;
  z-index: 9;
  width: 24px;
  transition: all .8s cubic-bezier(.23, 1, .32, 1);
  position: absolute;
}

.animated-button .arr-1 {
  top: 6px;
  right: 10px;
}

.animated-button .arr-2 {
  top: 6px;
  left: -25%;
}

.animated-button .circle {
  opacity: 0;
  background-color: #f39a74;
  border-radius: 50%;
  width: 20px;
  height: 20px;
  transition: all .8s cubic-bezier(.23, 1, .32, 1);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.animated-button .text {
  z-index: 1;
  transition: all .8s cubic-bezier(.23, 1, .32, 1);
  position: relative;
  transform: translateX(-12px);
}

.animated-button:hover {
  color: #585858;
  border-radius: 12px;
  box-shadow: 0 0 0 12px #0000;
}

.animated-button:hover .arr-1 {
  right: -25%;
}

.animated-button:hover .arr-2 {
  left: 16px;
}

.animated-button:hover .text {
  transform: translateX(12px);
}

.animated-button:hover svg {
  fill: #585858;
}

.animated-button:active {
  scale: .95;
  box-shadow: 0 0 0 4px #f39a74;
}

.animated-button:hover .circle {
  opacity: 1;
  width: 220px;
  height: 220px;
}

.fancy {
  box-sizing: border-box;
  color: #fff;
  cursor: pointer;
  float: right;
  letter-spacing: .05em;
  text-align: center;
  text-transform: none;
  -webkit-user-select: none;
  user-select: none;
  background-color: #0000;
  border: 2px solid #585858;
  border-radius: 0;
  outline: none;
  margin: 0;
  padding: 1.25em 2em;
  font-size: 13px;
  font-weight: 700;
  text-decoration: none;
  transition: all .3s ease-in-out;
  display: inline-block;
  position: relative;
  overflow: visible;
}

.fancy:before {
  content: " ";
  transform-origin: center;
  background: #585858;
  width: 1.5625rem;
  height: 2px;
  transition: background .3s linear, width .3s linear;
  position: absolute;
  top: 50%;
  left: 1.5em;
  transform: translateY(-50%);
}

.fancy .text-button {
  text-align: left;
  text-transform: uppercase;
  color: #585858;
  padding-left: 2em;
  font-size: 1.125em;
  line-height: 1.33333em;
  text-decoration: none;
  transition: all .3s ease-in-out;
  display: block;
}

.fancy .top-key {
  background: #fff;
  width: 1.5625rem;
  height: 2px;
  transition: width .5s ease-out, left .3s ease-out;
  position: absolute;
  top: -2px;
  left: .625rem;
}

.fancy .bottom-key-1 {
  background: #fff;
  width: 1.5625rem;
  height: 2px;
  transition: width .5s ease-out, right .3s ease-out;
  position: absolute;
  bottom: -2px;
  right: 1.875rem;
}

.fancy .bottom-key-2 {
  background: #fff;
  width: .625rem;
  height: 2px;
  transition: width .5s ease-out, right .3s ease-out;
  position: absolute;
  bottom: -2px;
  right: .625rem;
}

.fancy:hover {
  color: #fff;
  background: #585858;
}

.fancy:hover:before {
  background: #fff;
  width: .9375rem;
}

.fancy:hover .text-button {
  color: #fff;
  padding-left: 1.5em;
}

.fancy:hover .top-key {
  width: 0;
  left: -2px;
}

.fancy:hover .bottom-key-1, .fancy:hover .bottom-key-2 {
  width: 0;
  right: 0;
}

