.body {
  position: relative;
}

.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: rgba(0, 0, 0, 0.2);
  visibility: visible;
  opacity: 1;
  transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1);

  z-index: 1000;
}

.backdrop.is-hidden {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
}

.backdrop.is-hidden .modal {
  transform: translate(-50%, -50%) scale(0.9);
}

.modal {
  position: absolute;
  left: 50%;
  top: 50%;
  width: 100%;
  max-width: 450px;
  max-height: 609px;
  padding: 40px 40px 20px 40px;
  background: $whiteColor;
  box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.12), 0px 1px 1px rgba(0, 0, 0, 0.14), 0px 2px 1px rgba(0, 0, 0, 0.2);
  border-radius: 4px;
  transform: translate(-50%, -50%) scale(1);
  transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
  
}

.modal__close {
  position: absolute;
  top: 8px;
  right: 8px;
  height: 50px;
  width: 50px;  
  cursor: pointer;
  border: none;
  background-color: transparent;
}

.modal__close--icon {
  display: inline-block;
  fill: $mainColor;
  transition: fill 250ms cubic-bezier(0.4, 0, 0.2, 1);
  
  height: 40px;
  width: 40px;
  position: absolute;
  top: 4px;
  right: 4px;

  &:hover, &:focus {
fill: $secondaryColor;
  }
}

.modal__form--title {
  display: block;
  font-family: $fontMain;
  font-weight: 700;
  color: $mainColor;
  font-size: 20px;
  line-height: 1.5;
  letter-spacing: 0.03em;
  text-align: center;
  margin-bottom: 12px;
  width: 370px;
  padding-left: 40px;
}

.modal__form--details {
  margin-bottom: 20px;
}

.modal__form--field {
  display: flex;
  flex-direction: column;
  position: relative;
  margin-bottom: 10px;
}

.modal__form--label {
  font-family: $fontMain;
  font-size: 12px;
  line-height: 1.16;
  letter-spacing: 0.01em;
  margin-bottom: 4px;
}

.modal__form--input {
  height: 40px;
  padding: 11px 42px;
  outline: transparent;
  border: 1px solid rgba(33, 33, 33, 0.2);
  border-radius: 4px;
  transition: border 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.modal__form--input::placeholder {
  font-family: $fontMain;
  font-size: 12px;
  line-height: 1.17;
  letter-spacing: 0.05em;
  color: $secondaryColor;
  &:hover {
  border: 1px solid $accentColor;
}
}

.modal__form--input:hover,
.modal__form--input:focus {
  border: 1px solid $blueHover;
}


.form__input--icon {
  position: absolute;
  left: 12px;
  bottom: 12px;
  fill: $secondaryColor;
  opacity: 0.7;
  transition: fill 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.modal__form--input:hover+.form__input--icon,
.modal__form--input:focus+.form__input--icon {
  fill: $blueHover;
}

.form__input--comment {
  height: 120px;
  resize: none;
  padding: 12px 16px;
  outline: transparent;
  border: 1px solid rgba(33, 33, 33, 0.2);
  border-radius: 4px;
  transition: border 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.form__input--comment:hover,
.form__input--comment:focus,
.form__input--comment:checked {
  border: 1px solid $blueHover;
}

.form__input--comment::placeholder {
  font-family: $fontMain;
  font-size: 12px;
  line-height: 1.16;
  letter-spacing: 0.01em;
  color: rgba(117, 117, 117, 0.5);
}
.modal__form {
  height: 460px;
}
.modal__form--btn {
  display: block;
  margin: 0 auto;
  margin-top: 30px;
}